import React from 'react'
import { themePostTracking } from '../../../../actions/themeActions';
import { useState } from 'react';
import { GET_SHOPIFY_PRODUCT_DETAILS } from '../../../../actions/addToCartAction';

const PopupProductCard = ({ product, productLoading, showAddToCart, wall, ugcSetting, onsetProductImg, openAddtocart, item, setOnProductDetails }) => {
    const [noImg, setNoImg] = useState(false)
    const [loading, setLoading] = useState(false)
    const ownerId = wall.User.id;
    const tracking=( wall, product_id)=>{ 
        themePostTracking({ type: 1, action: 2, wall: wall?.Wall?.id, product_id: product_id , post:item.referenceId})
    }

    const checkForShopify = async() => {
        setLoading(true)
        const itemDetails = {};
        console.log('checkForShopify')
        if (product && product.UgcProduct && product.UgcProduct.product_url) {
            try {
                GET_SHOPIFY_PRODUCT_DETAILS(product.UgcProduct.product_url)
                        .then(res => {
                            if (res.data) {
                                itemDetails[product.id] = res.data;
                                setLoading(false)
                                setOnProductDetails(itemDetails)
                                openAddtocart(product.id, product)
                        }
                        })
                        .catch(error => {
                            // Handle error                console.log('error', error)
                            console.log('error', error)
                            openUrl(product.UgcProduct.product_url)
                            setLoading(false)
                        });
                

            } catch (error) {
                console.log('error', error)
                openUrl(product.UgcProduct.product_url)
                setLoading(false)
                
            }
        }
    }

    const openUrl = (url) => {
        const UgcSetting = item.ugc_personalizaion.UgcSetting;
        var newUrl = url;
        if(wall?.User?.id == 114259){
           newUrl +=`?utm_source=tagshop&utm_medium=gallery&utm_campaign=${item.network.name}`
        }else{
            if(wall?.Wall.utm_status==1) newUrl +=`?utm_source=${wall?.Wall.utm_source}&utm_medium=${wall?.Wall.utm_medium}&utm_campaign=${wall?.Wall.utm_campaign}`
        }

        if(parseInt(UgcSetting.tab_target) == 2) window.parent.location.href = newUrl
        else window.open(newUrl, '_blank');
      }

    return (
        <div className={`ts_reelTheme_Product ${noImg ? "ts_reelTheme_No_Product" : ""}`}>
           {noImg ? <></> :<div className='ts_reelTheme_rProductImage'>
                <img src={product.UgcProduct.product_image} alt=""  onError={() => setNoImg(true)} />
            </div>}
            <div className="ts_reelTheme_rProductDetail">
                <div className="ts_reelTheme_rProductTitle">
                    {product.UgcProduct.product_title}
                </div>
                <div className="ts_reelTheme_rProductPrice">
                    <div className="ts_reelTheme_rProductPrice_disconted">{product.UgcProduct.price_currency_symbol}{" "}{product.UgcProduct.product_discount > 0 ? product.UgcProduct.product_discount : product.UgcProduct.product_price}</div>
                    {product.UgcProduct.product_price == product.UgcProduct.product_discount ? <></> : <div className="ts_reelTheme_rProductPrice_original">{product.UgcProduct.price_currency_symbol}{" "}{product.UgcProduct.product_price}</div>}
                </div>

                {/* {!productLoading ? */}
                    <button className={`ts_reelTheme_btn ${ownerId == 30656 ? 'homepage-ig-product-link' : ''} ${loading ? 'ts_reelTheme_spinner ts_reelTheme_spinner-white ts_reelTheme_spinner-right' : ""}`} disabled={loading} onClick={() => { checkForShopify(); onsetProductImg(product.UgcProduct.product_image); tracking(wall, product.UgcProduct.id) }}>{ugcSetting.buy_now_text} </button>
                    {/* : <></>
                } */}
                {/* {!productLoading ?
                    showAddToCart ?
                        <button className={`ts_reelTheme_btn ${ownerId == 30656 ? 'homepage-ig-product-link' : ''}`} onClick={() => {openAddtocart(product.id, product); onsetProductImg(product.UgcProduct.product_image); tracking( wall, product.UgcProduct.id)}}>{ugcSetting.buy_now_text}</button>
                        :
                        <button className={`ts_reelTheme_btn ${ownerId == 30656 ? 'homepage-ig-product-link' : ''}`} onClick={()=>{openUrl(product.UgcProduct.product_url); tracking( wall, product.UgcProduct.id)}}>{ugcSetting.buy_now_text}</button>
                    :
                    <></>} */}
                {/* <button className="ts_reelTheme_btn">Shop NOW</button> */}
            </div>
        </div>
    )
}

export default PopupProductCard