import React, { Component } from 'react'
import { SHOPIFY_CART_COUNT_ITEM } from '../../../../../actions/addToCartAction'
import { CLIENT_HOST } from '../../../../../actions/api'
import { NO_PRODUCT_IMAGE } from '../../../../../constants'

export default class continueShopping extends Component {
  state = { cartItems:1}

  componentDidMount(){
    SHOPIFY_CART_COUNT_ITEM().then(res => {
      const {item_count} = res.data 
      if(item_count)  this.setState({cartItems:item_count})
  }).catch(err => {
      
  })
  }

  render() {
    const { productDetails, closeAddtocart, productImg, wall, tagshopProductDeatil } = this.props 
    return (
      <div className="ts_rt_proceedCheckout_modal" aria-hidden="true">
        <div className="ts_rt_proceedCheckout_modalDialog" role="document">
          <div className="ts_rt_proceedCheckout_close_btn" onClick={closeAddtocart}>
            <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15" fill="none">
              <path d="M14.5352 14.4531C14.2773 14.7109 13.8047 14.7109 13.5469 14.4531L7.875 8.73828L2.16016 14.4531C1.90234 14.7109 1.42969 14.7109 1.17188 14.4531C0.914062 14.1953 0.914062 13.7227 1.17188 13.4648L6.88672 7.75L1.17188 2.07812C0.914062 1.82031 0.914062 1.34766 1.17188 1.08984C1.42969 0.832031 1.90234 0.832031 2.16016 1.08984L7.875 6.80469L13.5469 1.08984C13.8047 0.832031 14.2773 0.832031 14.5352 1.08984C14.793 1.34766 14.793 1.82031 14.5352 2.07812L8.82031 7.75L14.5352 13.4648C14.793 13.7227 14.793 14.1953 14.5352 14.4531Z" fill="white"></path>
            </svg>
          </div>
          <div className="ts_rt_proceedCheckout_modalContent">
            <div className="ts_rt_proceedCheckout_modalBody">
              <div className="ts_rt_proceedCheckout_modalBody_inner">
                <div className="ts_rt_proceedCheckout_productImage">
                  <img src={productDetails.featured_image ? productDetails.featured_image.src : productImg} alt="product img" onError={e => e.target.src = NO_PRODUCT_IMAGE} />
                </div>
                <div className="ts_rt_proceedCheckout_productDetail">
                  <div className="ts_rt_proceedCheckout_productTitle">{productDetails.name}</div>
                  <div className="ts_rt_proceedCheckout_productPrice">{tagshopProductDeatil?.UgcProduct?.price_currency_symbol}{tagshopProductDeatil?.UgcProduct.product_discount>0?tagshopProductDeatil?.UgcProduct.product_discount: tagshopProductDeatil?.UgcProduct.product_price}</div>
                  {this.state.cartItems ? <div className="ts_rt_proceedCheckout_product_added">
                    Your cart now has {this.state.cartItems} item
                  </div> : <></>}
                  <div className="ts_rt_proceedCheckout_actions">
                    <div className="ts_rt_proceedCheckout_actions_checkOut" onClick={() => window.location = `${CLIENT_HOST}/cart`}>Proceed to checkout</div>
                    <div className="ts_rt_proceedCheckout_actions_shopping" onClick={closeAddtocart}>Continue Shopping</div>
                    {wall && wall.UserRule && wall.UserRule.taggBranding == `1` ? <div className="ts_rt_branding ts_rt_branding_addToCart">
                      <img src="http://cloud.taggbox.com/taggshop/app/assets/media/tagshop-branding.svg" alt="brand logo" />
                    </div> : <></>}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
    )
  }
}
