import React, { useEffect, useRef, useState } from 'react';
import Iframe from 'react-iframe';
import ReactPlayer from "react-player"
import { connect } from 'react-redux';
import { showHotspot } from '../../../../actions/themeActions';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO } from '../../../../utils'
import Image from './Media/Image'

import Tiktoks from './tiktok'
import Hotspot from '../../../Themes/Elements/HotSpot';
import { SET_MEDIA_SIZE } from '../../../../reducers/reducersKeys';

const PopUpMedia = ({item, setMediaSize, ugc_products, isVideoError, wall, custmClass, ImageUrl, videoUrl, showHotspot, mediaWidth, mediaHeight, inViewport, handleProgress, muted}) => {
    const [loadedImg, setLoadedImg] = useState(false)
    const [mediaLoaded, setMediaLoaded] = useState(false)
    const [imgUrl, setImgUrl] = useState(ImageUrl)
    const [progress, setProgress] = useState(0)
    const [played, setPlayed] = useState(0)
    const [volume, setVolume] = useState(0.8)
    const [vUrl, setVideoUrl] = useState(videoUrl)
    const [videoLoaded, setVideoLoaded] = useState(false)
    const myRef = useRef()
    const playerRef = useRef()

    useEffect(() => {
        if (imgUrl !== ImageUrl) {
            setImgUrl(ImageUrl)
            setMediaLoaded(false)
        }
    }, [ImageUrl])

    useEffect(() => {
    }, [inViewport])
    

    const updateMediaSize = (e) => {
        const mediaHeight = e.target.offsetHeight
        const mediaWidth = e.target.offsetHeight
        setMediaSize({ mediaHeight, mediaWidth })
        setMediaLoaded(true)
    }

       
        return (
            <div className="tb_media_holder" key={`postID${item.id}`}>
                <>
                    {(item.type === 2 || item.type === 4) ?
                        <div className="tb_img_holder" ref={myRef} onMouseEnter={() => showHotspot({ showAll: true })} onMouseLeave={() => showHotspot({ showAll: false })} style={{ minHeight: !mediaLoaded ? mediaHeight : null, minWidth: !mediaLoaded ? mediaWidth : null, backgroundColor: '#000', display: 'flex', alignItems: 'center' }}>
                            {(item.type == 2 || item.type == 4) && (item.hotspot == 1 && item.ugc_personalizaion.UgcSetting.status == 1) ? <Hotspot item={item} ugc_products={ugc_products} UgcSetting={item.ugc_personalizaion.UgcSetting} /> : null}

                            <img src={ImageUrl} data-src={ImageUrl} onLoad={(e) => {setLoadedImg(true);updateMediaSize(e)}} onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, () => { })
                            } data-network={item.networkId} data-filter-id={item.filterId} data-stories={item.stories} data-load="0" data-item-id={item.id} data-wall-id={wall.Wall.id} alt='popupImg' />
                        </div>
                        :
                        <div className="tb_img_holder" style={{backgroundColor: '#000'}}>
                            <div className="tb_modal_img_holder_001">
                            {videoLoaded ? <></> : <div className="ts_reelTheme_overlay_loader"></div>}
                            {inViewport ?
                                <ReactPlayer
                                    style={videoLoaded ? { display: 'contents', backgroundColor: '#000' } : { display: 'none' }}
                                    ref={playerRef}
                                    className="tb-detail-image-iframe"
                                    config={{
                                        facebook: {
                                          appId: '117029975354072',
                                          attributes: {
                                            'data-width': 'auto',
                                            'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`
                                          }
                                        },
                                        file: {
                                            forceVideo: true,
                                            attributes:{
                                                muted,
                                                // onError: (e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e)
                                            }
                                        }
                                      }}
                                    url={inViewport ? vUrl ? vUrl : "www.sdklfjlksdhfkld.sdkkfjlsdh" : ''}
                                    // url={videoUrl ? videoUrl : "www.sdklfjlksdhfkld.sdkkfjlsdh"}
                                    progressInterval={100}
                                    data-type="video"
                                    data-stories={item.stories}
                                    data-filter-id={item.filterId}
                                    data-network={item.networkId}
                                    data-link={item.link}
                                    data-wall-id={wall.Wall.id} 
                                    data-item-id={item.id}
                                    data-load={'0'}
                                    loop
                                    volume={volume}
                                    onReady={(e) => { setVideoLoaded(true);}}
                                    muted={muted}
                                    onProgress={e => handleProgress(e)}
                                    onError={e => IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e)}
                                    playing={inViewport}
                                    playsinline={true}
                                    width={'auto'}
                                    height={'100%'}
                                />
                                : <></>
                            }
                                {videoLoaded
                                    ? <></>
                                    :
                                    // <div className="tb_img_holder" >
                                    <>
                                        <div className='ts_reelTheme_spinner ts_reelTheme_spinner-white ts_reelTheme_spinner-right' ></div>
                                        <img src={ImageUrl} data-src={ImageUrl} onLoad={(e) => { setLoadedImg(true); updateMediaSize(e) }} onError={(e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT(e, () => { })
                                        } data-network={item.networkId} data-filter-id={item.filterId} data-stories={item.stories} data-load="0" data-item-id={item.id} data-wall-id={wall.Wall.id} height={"100%"} alt='popupImg' style={{maxWidth: 'fit-content', maxHeight: "100%"}} />
                                        </>
                                    // </div>
                                }
                                </div>
                        </div>
                    }
                </>
            </div>
        );
}

const mapStateToProps = state => ({
    mediaHeight: state.modalPop.mediaHeight,
    mediaWidth: state.modalPop.mediaWidth
})
const mapDispatchToProps = dispatch => {
    return {
        setMediaSize: (data) => dispatch({ type: SET_MEDIA_SIZE, payload: data }),
        showHotspot: (data) => dispatch(showHotspot(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PopUpMedia);