import React, { Component } from 'react'
import { SHOPIFY_ADD_TO_CART, TRACK_ADD_TO_CART } from '../../../../../actions/addToCartAction'
import { NO_CAMERA_IMAGE, NO_PRODUCT_IMAGE } from '../../../../../constants'
import { source } from '../../../../../actions/http-client'
// import Button from 'react-bootstrap/Button';
// import Modal from 'react-bootstrap/Modal';

export default class addToCart extends Component {
    state = { allSizes: null, allColors: null, selectedColor: null, selectedSize: null, availableSize: [], productImage: '', optionError: false }
    componentDidMount() {
        const { productDetails } = this.props
        this.getOptions()
        this.getProductImage()
    }
    getOptions = () => {
        const { productDetails } = this.props
        var colors = []
        var size = []
        if (productDetails && productDetails.options && productDetails.options.length) {
            productDetails.options.map(option => {
                if (option.values && option.values.length) {
                    // options[option.name] = option.values
                    if (option.name.toLowerCase() == 'color') { colors = option.values; this.setState({ selectedColor: option.values[0] }) }
                    if (option.name.toLowerCase() == 'size') size = option.values
                    else { colors = option.values; this.setState({ selectedColor: option.values[0] }) }
                }
            })
        }
        this.setState({ allSizes: size, allColors: colors })
        if (!colors || !colors.length) {
            var availableSize = []
            productDetails.variants.map(vr => {
                if (vr.available) {
                    if (!availableSize.includes(vr.option1)) availableSize.push(vr.option1)
                }
            })
            this.setState({ availableSize })

        }
        else if(size && size.length) this.checkAvablity(colors[0])
    }

    checkAvablity = color => {
        const { variants } = this.props.productDetails
        this.getProductImage()
        var availableSize = []
        variants.map(vr => {
            if (vr.option1.toLowerCase() == color.toLowerCase() && vr.available) {
                if (!availableSize.includes(vr.option2)) availableSize.push(vr.option2)
            }
            if (vr.option2?.toLowerCase() == color.toLowerCase() && vr.available) {
                if (!availableSize.includes(vr.option1)) availableSize.push(vr.option1)
            }
        })
        this.setState({ availableSize })

    }

    onAddToCart = () => {
        const { variants } = this.props.productDetails
        const { selectedColor, selectedSize, allColors, allSizes } = this.state
        if ((!selectedSize || selectedSize == '') && (allSizes && allSizes.length)) {
            this.setState({ optionError: true })
            return
        }
        if (variants.length == 1) {
            this.props.onSelectedVariant(variants[0])
            this.addProductsToCart(variants[0])
        }
        else {
            variants.map(vr => {
                if (!selectedColor || !selectedColor.length) {
                    if (vr.options.includes(selectedSize)) {
                        this.props.onSelectedVariant(vr)
                        this.addProductsToCart(vr)
                    }
                }
                else {
                    if (vr.options.includes(selectedColor) && vr.options.includes(selectedSize)) {
                        this.props.onSelectedVariant(vr)
                        this.addProductsToCart(vr)
                    }
                    else if(vr.options.includes(selectedColor)) {
                        this.props.onSelectedVariant(vr)
                        this.addProductsToCart(vr)
                    }
                }
            })
        }
    }

    addProductsToCart = (variant) => {
        const { wall, productDetails,tagshopProductDeatil } = this.props
        const payload = new FormData()
        payload.append('id', variant.id)
        payload.append('quantity', 1)
        SHOPIFY_ADD_TO_CART(payload).then(res => {
            const obj = {
                variant: variant.options ? variant.options.join(",") : "",
                price: variant.price / 100,
                wall: wall.Wall.wallId,
                website_id: source == 'website' ? wall.Wall.id : 0,
                product_id: tagshopProductDeatil.UgcProduct.id,
                quantity:1,
                post: tagshopProductDeatil.post_id
               }
               TRACK_ADD_TO_CART(obj)
            this.props.onAddToCart()
        }).catch(err => {
            // const obj = {
            //     variant: variant.options ? variant.options.join(",") : "",
            //     price: variant.price / 100,
            //     wall: wall.Wall.wallId,
            //     website_id: wall.Wall.wallId,
            //     product_id: tagshopProductDeatil.id,
            //     quantity:1
               
            //    }
            //    TRACK_ADD_TO_CART(obj)
            this.props.onAddToCart()
        })
    }

    getColorImage = (color) => {
        const { productDetails } = this.props
        if(color){
            const result = productDetails.variants.find(vr => vr.options.includes(color))
            // this.setState({ productImage: result.featured_image ? result.featured_image?.src : this.props.productImg})
            return result.featured_image ? result.featured_image?.src : this.props.productImg
        }
    }

    getProductImage = () => {
        const { productDetails } = this.props
        const { selectedColor } = this.state
        if (selectedColor) {
            // const result = productDetails.variants.find(vr => {
            //     if (vr.options.includes(selectedColor)) {
            //         // this.props.onSelectedVariant(vr)
            //         // this.addProductsToCart(vr.id)
            //         // return vr.featured_image ? vr.featured_image?.src : false
            //         this.setState({ productImage: vr.featured_image ? vr.featured_image?.src : "" })
            //         // return false
            //     }
            // })
            const result = productDetails.variants.find(vr => vr.options.includes(selectedColor))
            this.setState({ productImage: result.featured_image ? result.featured_image?.src : this.props.productImg})
        }
        else { this.setState({ productImage: productDetails.featured_image }) }
    }


    render() {
        const { closeAddtocart, addtoCartPopup, productDetails, onSelectedVariant , productImg, wall, tagshopProductDeatil} = this.props
        const { allColors, allSizes, selectedColor, availableSize, selectedSize, productImage, optionError } = this.state
        return (
            <div className="ts_rt_addToCart_mobile_modal" aria-hidden="true">
                <div className="ts_rt_addToCart_mobilePopup_modalDialog" role="document">
                    <div className="ts_rt_addToCart_mobilePopup_close_btn" onClick={closeAddtocart}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15" fill="none">
                            <path d="M14.5352 14.4531C14.2773 14.7109 13.8047 14.7109 13.5469 14.4531L7.875 8.73828L2.16016 14.4531C1.90234 14.7109 1.42969 14.7109 1.17188 14.4531C0.914062 14.1953 0.914062 13.7227 1.17188 13.4648L6.88672 7.75L1.17188 2.07812C0.914062 1.82031 0.914062 1.34766 1.17188 1.08984C1.42969 0.832031 1.90234 0.832031 2.16016 1.08984L7.875 6.80469L13.5469 1.08984C13.8047 0.832031 14.2773 0.832031 14.5352 1.08984C14.793 1.34766 14.793 1.82031 14.5352 2.07812L8.82031 7.75L14.5352 13.4648C14.793 13.7227 14.793 14.1953 14.5352 14.4531Z" fill="white"></path>
                        </svg>
                    </div>
                    <div className="ts_rt_addToCart_mobilePopup_modalContent">
                        <div className="ts_rt_addToCart_mobilePopup_modalBody">
                            <div className="ts_rt_addToCart_mobilePopup_modalBody_inner">
                                <div className="ts_rt_addToCart_mobilePopup_product_deatil">
                                    <div className="ts_rt_addToCart_mobilePopup_productImage">
                                        <img src={productImage ? productImage : NO_PRODUCT_IMAGE} alt="product img" onError={e => this.setState({productImage: NO_PRODUCT_IMAGE})} />
                                    </div>
                                    <div className="ts_rt_addToCart_mobilePopup_productDeatil_rightAlign">
                                        <div className="ts_rt_addToCart_mobilePopup_productTitle">{productDetails.title}</div>
                                        <div className="ts_rt_addToCart_mobilePopup_productPrice">{tagshopProductDeatil?.UgcProduct?.price_currency_symbol}{tagshopProductDeatil?.UgcProduct.product_discount>0?tagshopProductDeatil?.UgcProduct.product_discount: tagshopProductDeatil?.UgcProduct.product_price}</div>
                                        {/* <div className="ts_rt_addToCart_mobilePopup_btn ">Shop Now</div> */}
                                    </div>
                                </div>
                                <div className="ts_rt_addToCart_mobilePopup_productDetail">
                                    
                                    {optionError ? <div className="ts_addToCart_selectOptionsError">Please select any one variant to proceed!</div> : <></>}
                                    <div className="ts_rt_addToCart_mobilePopup_colorPalate_section">
                                        {allColors && allColors.length ?
                                            <>
                                                <div className="ts_rt_addToCart_mobilePopup_colorHeading">
                                                    Color:
                                                </div>
                                                <div className="ts_rt_addToCart_mobilePopup_colorPalate">
                                                    {allColors.map(color => (
                                                        <div className={`ts_rt_addToCart_mobilePopup_color ${selectedColor == color ? 'active' : ''}`}>
                                                            <div className="ts_rt_addToCart_mobilePopup_colorTile" style={{ backgroundColor: color }} onClick={() => this.setState({ selectedColor: color }, () => this.checkAvablity(color))}><img src={this.getColorImage(color)} style={{height: '100%', width: '100%'}} onError={e => e.target.src = NO_PRODUCT_IMAGE} /></div>
                                                        </div>
                                                    ))}
                                                </div>
                                            </>
                                            : <></>}
                                    </div>
                                    {allSizes && allSizes.length ? <div className="ts_rt_addToCart_mobilePopup_sizeChart">
                                        <div className="ts_rt_addToCart_mobilePopup_sizeChart_label">
                                            Size
                                        </div>
                                        {allSizes.map(size => (
                                            availableSize.includes(size) ?
                                                <div className={`ts_rt_addToCart_mobilePopup_sizebutton  ${size == selectedSize ? 'active' : ''}`} onClick={() => this.setState({ selectedSize: size, optionError: false })}>
                                                    {size}
                                                </div>
                                                : <div className="ts_rt_addToCart_mobilePopup_sizebutton ts_productSize_unvavailable">
                                                    <span className="ts_rt_addToCart_mobilePopup_productSize_unvavailable_cross"></span>
                                                    {size}
                                                </div>
                                        ))}
                                    </div>
                                        : <></>}
                                    <div className="ts_rt_addToCart_actions">
                                        <div className="ts_rt_addToCart_actions_cancel" onClick={closeAddtocart}>Cancel</div>
                                        <div className="ts_rt_addToCart_actions_addCart" onClick={this.onAddToCart}>ADD TO CART</div>
                                    </div>
                                    {wall && wall.UserRule && wall.UserRule.taggBranding == `1` ? <div className="ts_rt_branding ts_rt_branding_addToCart">
                                        <img src="http://cloud.taggbox.com/taggshop/app/assets/media/tagshop-branding.svg" alt="brand logo" />
                                    </div> : <></>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}
