class PostPopUp extends PureComponent {

    constructor(props) {
        super(props);
        this.sliderRef = React.createRef();
        this.state = { currentIndex: this.props?.currentIndex, item: this.props.item, isVideoError: false, windowWidth: window.innerWidth, isAuthorImagevalid: true, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link), isHotspot: false, addtoCartPopup: false, allProductDetails: null, selectedProductId: null, selectedProductDetails: null, continueShopping: false, selectedVariant: null, showAddToCart: false, productLoading: true, productImg: '', tagshopProductDeatil: null }
        this.splideRef = React.createRef()
      }

    componentDidMount() {
        const { currentIndex, item, slideWithArrowKeys } = this.props;
        const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });

        var systemKeyEnable = document.querySelector("#modalPost0");
        if (systemKeyEnable) { systemKeyEnable.focus(); }
        document.addEventListener("keydown", slideWithArrowKeys, {passive: true});

    }

    setOnProductDetails = (data) => {
        this.setState({allProductDetails: data})
    }


    componentWillReceiveProps(nextProps) {
        const { currentIndex, item } = nextProps; const isAuthorImagevalid = isValidAuthorImage(item.author.picture);
        this.setState({ currentIndex: currentIndex, item: item, isVideoError: false, isAuthorImagevalid: isAuthorImagevalid, instaImg: IMG_DATA_FILTER(this.props.imgData, this.props.item.link) });
    }
    onErrorVideo = (e) => this.setState({ isVideoError: true })
    onMediaUpdate = (e) => this.setState({ isAuthorImagevalid: false });
    onAddToCart = (id, product) => {
        this.setState({ selectedProductDetails: this.state.allProductDetails[id], tagshopProductDeatil: product })
    }

    onUpdateData = () => {
        setTimeout(() => this.requestData(), 1000)
      }
    
      requestData = () => {
        console.log('moredata-------------')
        const { wall, appendData, hasMoreData, loaderData } = this.props;
        const postCount = wall.ThemeRule.numberOfPosts;
        const timeStamp = Math.floor(Date.now() / 1000);
        if (hasMoreData && !loaderData.isShowMoreLoading) {
            console.log('--------------')
            this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent)
        }
      }

    onItemChange = (item) => {
        console.log('item', item)
        this.setState({item})
    }



    render() {
        const { personalization, onClosePopUp, slideWithArrowKeys, wall, webFilters, completeData, hasMoreData } = this.props;
        const { currentIndex, item, isVideoError, instaImg, addtoCartPopup, selectedProductDetails, continueShopping, selectedVariant, showAddToCart, productImg, productLoading, tagshopProductDeatil } = this.state;
        const ugc_products = convertObjectToArray(item.ugc_products);
        const networkData = findNetworkDataFromWebFilters(item.networkId, webFilters)
        const sliderSettings1 = {
            type: 'slider',
            perPage: 1,
            perMove: 1,
            pagination: false,
            direction: 'ttb',
            autoPlay: false,
            height: '100vh',
            arrows: false,
            start:currentIndex,
            snap:true

        }
        // console.log('this.props.item', this.props.item)

        return <>
            <PopUpStyles wall={wall} item={item} networkData={networkData} />
                <div className={`modalId${item.id} tb_modal_001 tb_fade ${item.type == 4 ||
                    item.type == 5 ||
                    (item.ugc_products && Object.keys(item.ugc_products).length > 0)
                    ? `modal-text-image`
                    : item.type == 2 || item.type == 3
                        ? `modal-only-image`
                        : `modal-only-text`
                    }`} onKeyDown={slideWithArrowKeys} onKeyPress={slideWithArrowKeys} onLoad={slideWithArrowKeys} onClick={(e) => e.currentTarget == e.target && onClosePopUp(e)} >
                    <Splide ref={this.splideRef} hasTrack={false} options={sliderSettings1} onDragged={() => this.getShopifyProducts(this.state.item)} onDrag={slide => {if(hasMoreData && (slide.index > completeData.length - 2)) this.requestData()}} id="post-slider">
                        <SplideTrack>
                        {
                            (completeData && completeData.length > 0) ? completeData.map((item, index) => (<SplideSlide key={`${item.id}-${index}`}>
                                <PopupBody key={`${item.id}-${index}`} onItemChange={this.onItemChange} onClosePopUp={onClosePopUp} wall={wall} item={item} instaImg={instaImg} showAddToCart={showAddToCart} openAddtocart={(id, product) => this.setState({ addtoCartPopup: true }, () => this.onAddToCart(id,product))} onsetProductImg={(img)=> this.setState({productImg:img})} productLoading={productLoading} sliderRef={this.sliderRef} personalization={personalization} ugc_products={ugc_products} isVideoError={isVideoError} addtoCartPopup={addtoCartPopup} continueShopping={continueShopping} setOnProductDetails={this.setOnProductDetails} />
                            </SplideSlide>
                            )) : <></>
                        }
                        </SplideTrack>
                    </Splide>
                </div>
                {addtoCartPopup && selectedProductDetails && !continueShopping ? <AddToCart productImg={productImg} addtoCartPopup={addtoCartPopup} productDetails={selectedProductDetails} tagshopProductDeatil={tagshopProductDeatil} closeAddtocart={() => this.setState({ addtoCartPopup: false, selectedProductDetails: null })} onAddToCart={() => this.setState({ continueShopping: true })} onSelectedVariant={(value) => this.setState({ selectedVariant: value })} wall={wall} /> : <></>}
            {continueShopping ? <ContinueShopping productImg={productImg} wall={wall} addtoCartPopup={addtoCartPopup} productDetails={selectedVariant} tagshopProductDeatil={tagshopProductDeatil} closeAddtocart={() => this.setState({ addtoCartPopup: false, continueShopping: false, selectedProductDetails: null })} /> : <></>}
        </>
    }
}

const mapStateToProps = state => {
    const { appData } = state;
    return {
      loaderData: state.loaderData,
      appendData: appData.postData.appendData,
      hasMoreData: appData.postData.hasMoreData[0] ? appData.postData.hasMoreData[0].hasMoreData == false ? false : appData.postData.hasMoreData[appData.postData.appendData.networkID] ? appData.postData.hasMoreData[appData.postData.appendData.networkID].hasMoreData : true : true
    }
  }
  
  const mapDispatchToProps = dispatch => {
    return {
      getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    }
  }
  export default connect(mapStateToProps, mapDispatchToProps)(PostPopUp);