import React, { useEffect } from 'react'
import PopUpMediaSlider from './Elements/PopUpMediaSlider'
import PopUpMedia from './Elements/PopUpMedia'
import PopUpBranding from './Elements/PopUpBranding'
import { useRef } from 'react'
import { useIntersection } from '../../../utils/useIntersection'
import PopupProductCard from './Elements/PopupProductCard'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/css';
import { useState } from 'react'
import { convertObjectToArray } from '../../../utils'
import ErrorFound from '../errorFallback'


const PopupBody = ({ wall, item, instaImg, ugc_products, isVideoError, personalization, productLoading, showAddToCart, onsetProductImg, openAddtocart, onClosePopUp, addtoCartPopup,continueShopping, onItemChange,setOnProductDetails }) => {
    const sliderRef = useRef()
    const [muted, setMuted] = useState(true)
    const [played, setPlayed] = useState(0)
    const inViewport = useIntersection(sliderRef, '-120px')

    const handleProgress = state => {
        setPlayed(state.played)
    }

    const handleToggleMuted = () => {
        setMuted(!muted)
    }
    


    return (
        <ErrorFound>
        <div className="tb_modal_dialog taggModal reelTheme_modalDialog" ref={sliderRef}>
            <div className="tb_conetent" style={{ height: '100%' }}>
                <div className="tb_modal_head">
                </div>
                <div className="tb_modal_body reelTheme_modalBody">
                    <div className="tb_modal_body_wrap" style={{ height: '100%' }}>
                        {item.file ? item.imageList && item.imageList.length > 0 ? <PopUpMediaSlider inViewport={inViewport} muted={muted} wall={wall} item={item} instaImg={instaImg} ugc_products={ugc_products} isVideoError={isVideoError} /> : <PopUpMedia muted={muted} handleProgress={handleProgress} wall={wall} item={item} inViewport={inViewport} instaImg={instaImg} ugc_products={ugc_products} isVideoError={isVideoError} custmClass={"tb_media_wrap_002"} ImageUrl={item.postFileNew} videoUrl={item.mediaUrl} active={true} /> : ''}
                        <div className="ts_reelTheme_overlay" style={item.imageList && item.imageList.length > 0 ? {pointerEvents: 'none'} : {}}>
                            {!(addtoCartPopup || continueShopping) ? <div className="ts_reel_close_btn" onClick={onClosePopUp}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15" fill="none">
                                    <path d="M14.5352 14.4531C14.2773 14.7109 13.8047 14.7109 13.5469 14.4531L7.875 8.73828L2.16016 14.4531C1.90234 14.7109 1.42969 14.7109 1.17188 14.4531C0.914062 14.1953 0.914062 13.7227 1.17188 13.4648L6.88672 7.75L1.17188 2.07812C0.914062 1.82031 0.914062 1.34766 1.17188 1.08984C1.42969 0.832031 1.90234 0.832031 2.16016 1.08984L7.875 6.80469L13.5469 1.08984C13.8047 0.832031 14.2773 0.832031 14.5352 1.08984C14.793 1.34766 14.793 1.82031 14.5352 2.07812L8.82031 7.75L14.5352 13.4648C14.793 13.7227 14.793 14.1953 14.5352 14.4531Z" fill="white" />
                                </svg>
                            </div> : <></>}

                            {/* -------reel progress----------- */}
                            <div className="ts_reel_progress_bar" style={{ width: `${played * 100}%` }}>
                            </div>

                            {/* ------------Mute/Unmute button----------- */}
                            
                            <div className="ts_reelTheme_bottom">
                                <div className="ts_rt_addToCart_modal_productSlider">
                                    {(item.type == 3 || item.type == 5) ? <button className="ts_reel_mute_button" onClick={handleToggleMuted}>
                                        {!muted ? <svg xmlns="http://www.w3.org/2000/svg" width="22" height="19" viewBox="0 0 22 19" fill="none">
                                            <path d="M12.0078 0.867188C12.4375 1.0625 12.75 1.53125 12.75 2V17C12.75 17.5078 12.4375 17.9375 12.0078 18.1719C11.8125 18.25 11.6562 18.25 11.5 18.25C11.1875 18.25 10.875 18.1719 10.6406 17.8594L5.36719 13.1719H2.125C1.07031 13.1719 0.25 12.3516 0.25 11.3359V7.625C0.25 6.60938 1.07031 5.75 2.125 5.75H5.36719L10.6406 1.10156C10.875 0.867188 11.1875 0.75 11.5 0.75C11.6562 0.75 11.8125 0.789062 12.0078 0.867188ZM11.4609 17L11.4219 2.03906L5.83594 7H2.125C1.77344 7 1.5 7.27344 1.5 7.625V11.375C1.5 11.6875 1.77344 12 2.125 12H5.83594L11.4609 17ZM18.1797 3.36719C20.25 4.77344 21.5 7.03906 21.5 9.5C21.5 11.9609 20.25 14.2656 18.1797 15.6719C18.0625 15.75 17.9453 15.75 17.8281 15.75C17.6328 15.75 17.4375 15.6719 17.3203 15.4766C17.125 15.2031 17.2031 14.8125 17.4766 14.6172C19.1953 13.4844 20.25 11.5703 20.25 9.5C20.25 7.46875 19.1953 5.55469 17.4766 4.42188C17.2031 4.22656 17.125 3.83594 17.3203 3.5625C17.5156 3.25 17.9062 3.17188 18.1797 3.36719ZM16.1094 6.49219C17.125 7.19531 17.75 8.32812 17.75 9.5C17.75 10.7109 17.125 11.8438 16.1094 12.5469C15.9922 12.625 15.875 12.625 15.7578 12.625C15.5625 12.625 15.3672 12.5469 15.25 12.3516C15.0547 12.0781 15.1328 11.6875 15.4062 11.4922C16.0703 11.0625 16.5 10.3203 16.5 9.5C16.5 8.71875 16.0703 7.97656 15.4062 7.54688C15.1328 7.35156 15.0547 6.96094 15.25 6.6875C15.4453 6.375 15.8359 6.29688 16.1094 6.49219Z" fill="white" />
                                        </svg>
                                        :
                                        <svg xmlns="http://www.w3.org/2000/svg" width="26" height="21" viewBox="0 0 26 21" fill="none">
                                            <path d="M14.711 3.03906L11.586 5.8125L10.5704 5.03125L13.8907 2.10156C14.1251 1.86719 14.4376 1.75 14.7501 1.75C14.9064 1.75 15.0626 1.78906 15.2579 1.86719C15.6876 2.0625 16.0001 2.53125 16.0001 3V9.28906L14.711 8.3125V3.03906ZM19.7501 10.5C19.7501 9.71875 19.3204 8.97656 18.6564 8.54687C18.3829 8.35156 18.3048 7.96094 18.5001 7.6875C18.6954 7.375 19.086 7.29687 19.3595 7.49219C20.3751 8.19531 21.0001 9.32812 21.0001 10.5C21.0001 11.3203 20.6876 12.0625 20.2579 12.6484L19.2814 11.9062C19.5548 11.5156 19.7501 11.0078 19.7501 10.5ZM23.5001 10.5C23.5001 8.46875 22.4454 6.55469 20.7267 5.42187C20.4532 5.22656 20.3751 4.83594 20.5704 4.5625C20.7657 4.25 21.1564 4.17187 21.4298 4.36719C23.5001 5.77344 24.7501 8.03906 24.7501 10.5C24.7501 12.1406 24.1642 13.7031 23.1876 14.9922L22.211 14.2109C23.0314 13.1562 23.5001 11.8672 23.5001 10.5ZM14.711 18V14.6797L16.0001 15.6953V18C16.0001 18.5078 15.6876 18.9375 15.2579 19.1719C15.0626 19.25 14.9064 19.25 14.7501 19.25C14.4376 19.25 14.1251 19.1719 13.8907 18.8984L8.61729 14.25H5.37511C4.32042 14.25 3.50011 13.4297 3.50011 12.375V8.625C3.50011 7.80469 4.04698 7.10156 4.82823 6.86719L6.27354 8H5.37511C5.02354 8 4.75011 8.27344 4.75011 8.625V12.375C4.75011 12.6875 5.02354 13 5.37511 13H9.08604L14.711 18ZM25.7267 19.4062C26.0001 19.6016 26.0782 19.9922 25.8048 20.2656C25.6876 20.4219 25.4923 20.5 25.336 20.5C25.2189 20.5 25.1017 20.4609 24.9845 20.3828L1.23448 1.63281C0.961043 1.4375 0.882918 1.00781 1.11729 0.773436C1.31261 0.499999 1.70323 0.421874 1.97667 0.656249L25.7267 19.4062Z" fill="white" />
                                        </svg>}
                                    </button> : <></>}
                                    {item.ugc_personalizaion.UgcSetting && item.ugc_personalizaion.UgcSetting.status == 1 && personalization.themeType == 1 && item.ugc_products &&
                                            Object.keys(item.ugc_products).length > 0 ? 
                                            <Swiper
                                        autoHeight={true}
                                        speed={400}
                                        edgeSwipeDetection={true}
                                        slidesPerView={1}
                                        touchRatio={2}
                                        // spaceBetween={20}
                                        direction='horizontal'
                                        className="reelThemeProduct_slider">
                                        { convertObjectToArray(item.ugc_products).map((product, index) => {
                                                return <SwiperSlide key={index}>
                                                    <PopupProductCard product={product} openAddtocart={openAddtocart} onsetProductImg={onsetProductImg} showAddToCart={showAddToCart} productLoading={productLoading} wall={wall} ugcSetting={item.ugc_personalizaion.UgcSetting}  item={item} setOnProductDetails={setOnProductDetails} />
                                                </SwiperSlide>
                                            })
                                        }
                                    </Swiper>
                                    : null}
                                </div>
                                {wall && wall.UserRule && wall.UserRule.taggBranding == `1` ? <div className="ts_rt_branding">
                                    <img src="http://cloud.taggbox.com/taggshop/app/assets/media/tagshop-branding.svg" alt="brand logo" />
                                </div> : <></>}
                            </div>
                        </div>
                    </div>
                </div>
                {wall && wall.UserRule && wall.UserRule.taggBranding == `1` ? <PopUpBranding /> : ''}
            </div>
        </div>
        </ErrorFound>
    )
}

export default React.memo(PopupBody)
